:root {
    --black: #000;
    --white: #fff;
    --burgundy-color: #B91122;
    --orange-color: #F93801;
    --purple-color: #8A60FF;
    --pink-color: #FFAC9C;
    --text-color: #646464;
    --title-color: #292019;
    --cta-color: #00566B;
    --footer-links: #9A9A9A;

    --poppins-font: 'Poppins', serif;
    --abnaya-font: 'Abhaya Libre', sans-serif;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container{
    max-width: 1260px;
